import Vue from "vue";
import Vuex from "vuex";
import VuexPersistence from "vuex-persist";

const DEBUG = process.env.NODE_ENV !== "production";

Vue.config.devtools = true;

Vue.use(Vuex);

const createId = () => Math.floor(Math.random() * Date.now());

const vuexLocal = new VuexPersistence({
  storage: window.sessionStorage,
});

const colors = [
  "#ffa31a",
  "#002080",
  "#0074d9",
  "#bd4b00",
  "#000000",
  "#004d4d",
  "#e8e8e8",
  "#9e9e9e",
  "#4d3319",
  "#8034eb",
  "#3cbd1c",
  "#990303"
];

const playerNames = ["Andreas", "Jinyu", "Jo", "Phil", "Tom"];
const players = playerNames.map((name, index) => (
  {
    id: createId(),
    name,
    color: colors[index],
  }
));

const store = new Vuex.Store({
  state: {
    config: {
      colors: [...colors],
    },
    game: {
      players: [...players],
      startedAt: 0,
      chronometer: 0,
      dice: [],
      diceLog: [],
      rollDisabled: false,
      twelveSidedDieEnabled: true,
    },
  },
  getters: {
    colors: state => state.config.colors.slice(),
    startedAt: state => state.game.startedAt,
    chronometer: state => state.game.chronometer,
    players: state => state.game.players.slice().reverse(),
    dice: state => state.game.dice.slice(),
    diceLog: state => state.game.diceLog.slice(),
    rollDisabled: state => state.game.rollDisabled,
    twelveSidedDieEnabled: state => state.game.twelveSidedDieEnabled,
  },
  mutations: {
    addPlayer(state, { name = "", color = "" }) {
      state.game.players.push({ id: createId(), name, color });
    },
    deletePlayer(state, player = {}) {
      if (state.game.players.length > 1) {
        state.game.players = state.game.players.filter(p => p.id !== player.id);
      }
    },
    playerNext(state) {
      const player = state.game.players.shift();
      state.game.players.push(player);
    },
    playerBack(state) {
      const player = state.game.players.pop();
      state.game.players.unshift(player);
    },
    setDice(state, player) {
      const id = createId();
      const timestamp = Date.now();

      const die1 = 1 + Math.floor(Math.random() * 6);
      const die2 = 1 + Math.floor(Math.random() * 6);
      const die3 = 1 + Math.floor(Math.random() * 12);
      const dice = [die1, die2, die3];

      state.game.dice = dice.slice();
      state.game.diceLog.unshift({
        id,
        timestamp,
        player,
        dice,
      });
    },
    updateChronometer(state) {
      if (!state.game.startedAt) {
        return;
      }
      const time = Math.floor((
        Date.now() - state.game.startedAt
      ) / 1000);
      if (time < Number.MAX_VALUE) {
        let hours = Math.floor(time / 3600);
        let minutes = Math.floor((
          time -
          (
            hours * 3600
          )
        ) / 60);
        if (hours && minutes < 10) {
          minutes = "0" + minutes;
        }
        state.game.chronometer =
          hours > 0 ?
            (
              hours + ":" + minutes
            ) :
            "" + minutes;
      } else {
        state.game.chronometer = 0;
      }
    },
    revertDice(state) {
      state.game.diceLog.shift();
      if (state.game.diceLog.length) {
        state.game.dice = state.game.diceLog[0].dice;
      }
    },
    updatePlayer(state, player = {}) {
      state.game.players.forEach(p => {
        if (p.id === player.id) {
          Object.assign(p, player);
        }
      });
    },
    updatePlayers(state, players = []) {
      state.game.players = players;
      state.game = { ...state.game }
    },
    setTwelveSidedDieEnabled(state, value) {
      state.game.twelveSidedDieEnabled = !!value;
    },
    setRollDisabled(state, value) {
      state.game.rollDisabled = value;
    },
    startGame(state) {
      state.game = {
        ...state.game,
        startedAt: Date.now(),
        chronometer: 0,
        dice: [],
        diceLog: [],
        rollDisabled: false,
      }
    },
    resetAll(state) {
      {
        state.game = {
          ...state.game,
          players: [...players],
          startedAt: 0,
          chronometer: 0,
          dice: [],
          diceLog: [],
          rollDisabled: false,
        }
      }
      sessionStorage.clear()
    }
  },
  actions: {
    rollDice({ commit, state }, player) {
      if (!state.game.diceLog.length) {
        commit("startGame");
      }
      if (state.game.rollDisabled) {
        return;
      }
      commit("setRollDisabled", true);
      commit("setDice", Object.assign({}, player || state.game.players[0]));
      setTimeout(() => commit("setRollDisabled", false), 1000);
      if (!player) {
        commit("playerNext");
      }
    },
    revertRoll({ commit, state }) {
      if (state.game.rollDisabled) {
        return;
      }
      commit("setRollDisabled", true);
      commit("revertDice");
      commit("playerBack");
      setTimeout(() => commit("setRollDisabled", false), 1000);
    },
  },
  strict: DEBUG,
  plugins: [vuexLocal.plugin],
});

export default store;
