<template>
  <div>
    <v-btn @click="dialog = true" icon>
      <v-icon>mdi-settings</v-icon>
    </v-btn>

    <v-dialog v-model="dialog" max-width="750px">
      <v-card>
        <v-card-title>
          <span class="headline">Settings</span>
        </v-card-title>
        <v-list class="mx-auto" width="80%" max-width="300">
          <v-list-item>
            <v-btn class="mx-auto mb-3" color="primary" block @click="onStartGame">Start New Game</v-btn>
          </v-list-item>
          <v-list-item>
            <v-btn class="mx-auto" color="primary" block @click="onResetAll">Clear cache / Reload</v-btn>
          </v-list-item>
          <v-list-item>
            <v-switch class="mx-auto" v-model="twelveSidedDieEnabled" label="Twelve sided die"></v-switch>
          </v-list-item>
        </v-list>
        <v-card-title>
          <span class="headline">Drag player to order list</span>
        </v-card-title>
        <div class="mx-auto" style="width: 95%">
          <grid
              :items="players"
              :draggable="true"
              :sortable="true">
            <template slot="cell" scope="props">
              <v-list-item @change="setPlayer(props)">
                <v-btn :color="props.item.color" class="mx-auto" fab small>
                  <span class="headline">{{ props.item.name[0] }}</span>
                </v-btn>
              </v-list-item>
            </template>
          </grid>
        </div>
        <v-card-actions>
          <v-btn class="mx-auto mb-5" color="grey.darken" @click="dialog = false">Close</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";

export default {
  name: "SettingsModal",
  data: () => (
      { dialog: false }
  ),
  computed: {
    players: {
      get() {
        return this.getPlayers();
      },
    },
    twelveSidedDieEnabled: {
      get() {
        return this.getTwelveSidedDieEnabled();
      },
      set(value) {
        this.setTwelveSidedDieEnabled(value);
      }
    },
  },
  methods: {
    ...mapGetters({
      getPlayers: "players",
      getTwelveSidedDieEnabled: "twelveSidedDieEnabled",
    }),
    ...mapMutations({
      startGame: "startGame",
      resetAll: "resetAll",
      updatePlayers: "updatePlayers",
      setTwelveSidedDieEnabled: "setTwelveSidedDieEnabled",
    }),
    onStartGame() {
      this.startGame();
    },
    onResetAll() {
      this.resetAll();
      setTimeout(() => window.location.reload(), 500)
    },
    setPlayer(data) {
      let players = this.getPlayers()
          .map(player => (
              { ...player }
          ));

      const player = players.splice(data.index, 1)[0];
      players.splice(data.sort, 0, player);

      players = players.reverse()
      this.updatePlayers(players);
    },
  }
};
</script>
